.breadcrumb li {
  list-style-type: none;
  display: inline-block;
}

.menu--main ul.menu {
  display: block;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: left;
}

.menu--main ul.menu li {
  list-style-type: none;
  display: inline-block;
}

.menu--main ul.menu li a {
  display: inline-block;
  padding: 0.5em 1.5em;
  color: #555;
  background-color: #FFF;
  text-transform: uppercase;
}

.menu--main ul.menu li a:hover {
  color: #555;
  background-color: #eee;
}

.menu--account ul.menu {
  display: block;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: left;
}

.menu--account ul.menu li {
  list-style-type: none;
  display: inline-block;
}

.menu--account ul.menu li:before {
  content: "|";
  display: inline-block;
  margin-right: 0.5em;
}

.menu--account ul.menu li:nth-child(1):before {
  content: "";
}

.menu--footer ul.menu {
  display: block;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: left;
}

.menu--footer ul.menu li {
  list-style-type: none;
  display: inline-block;
}

.menu--footer ul.menu li:before {
  content: "|";
  display: inline-block;
  margin-right: 0.5em;
}

.menu--footer ul.menu li:nth-child(1):before {
